import { GLOBALTYPES } from './globalTypes'
import { postDataAPI } from '../../utils/fetchData'
import valid from '../../utils/valid'
import axios from 'axios'
//import { useHistory } from 'react-router-dom';

export const login = (data) => async (dispatch) => {
    try {
        dispatch({ type: GLOBALTYPES.ALERT, payload: {loading: true} })
        const res = await postDataAPI('login', data)
        dispatch({ //est extrayendo el token de acceso de la respuesta del servidor y almacenndolo en la aplicacin cliente para su uso posterior en las solicitudes que requieran autenticacin.
           
            type: GLOBALTYPES.AUTH,  payload: {// En esa parte del cdigo, el token de acceso (access_token) y la informacin del usuario (user) se estn guardando en el estado global de la aplicacin bajo la accin GLOBALTYPES.AUTH. Esto significa que una vez que el usuario ha iniciado sesin correctamente, el token de acceso y la informacin del usuario se almacenan en el estado global de la aplicacin, lo que permite que la aplicacin acceda a esta informacin desde cualquier parte donde se necesite, como componentes de React o acciones posteriores en el flujo de la aplicacin
                token: res.data.access_token,
                user: res.data.user
            } 
        })

        localStorage.setItem("firstLogin", true)  //localStorage.setItem("firstLogin", true): Esta lnea de cdigo establece un valor en el almacenamiento local del navegador llamado "firstLogin" y le asigna el valor true. El propsito de esto parece ser marcar que el usuario ha iniciado sesin por primera vez. Esta marca puede ser til para identificar si es la primera vez que un usuario inicia sesin en la aplicacin, lo que puede ser til para realizar ciertas acciones o mostrar cierta informacin de bienvenida en futuras sesiones.
        dispatch({ 
            type: GLOBALTYPES.ALERT, 
            payload: {
                success: res.data.msg
            } 
        })
        
    } catch (err) {
        dispatch({ 
            type: GLOBALTYPES.ALERT, 
            payload: {
                error: err.response.data.msg
            } 
        })
    }
}


export const refreshToken = () => async (dispatch) => {//sta accin refreshToken se encarga de renovar el token de acceso utilizando el token de actualizacin almacenado en una cookie en el navegador, si el usuario ha iniciado sesin previamente (marcado por "firstLogin" en el almacenamiento local). Esto ayuda a mantener al usuario autenticado y a garantizar que puedan seguir accediendo a recursos protegidos de la aplicacin sin necesidad de volver a iniciar sesin manualmente.

 const firstLogin = localStorage.getItem("firstLogin")
    if(firstLogin){
        dispatch({ type: GLOBALTYPES.ALERT, payload: {loading: true} })

        try {
            const res = await postDataAPI('refresh_token')
            dispatch({ 
                type: GLOBALTYPES.AUTH, 
                payload: {
                    token: res.data.access_token,
                    user: res.data.user
                } 
            })

            dispatch({ type: GLOBALTYPES.ALERT, payload: {} })

        } catch (err) {
            dispatch({ 
                type: GLOBALTYPES.ALERT, 
                payload: {
                    error: err.response.data.msg
                } 
            })
        }
    }
}

export const register = (data) => async (dispatch) => {
    const check = valid(data);
    if (check.errLength > 0)
        return dispatch({ type: GLOBALTYPES.ALERT, payload: check.errMsg });

    try {
        dispatch({ type: GLOBALTYPES.ALERT, payload: { loading: true } });

      const res=  await axios.post('/user/register', data);
  
         

        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: {
                msg: res.data.msg
            },
        });
    } catch (err) {
        console.error('Error en el bloque catch:', err);
        dispatch({
            type: GLOBALTYPES.ALERT,
            payload: {
                error: err.response.data.msg,
            },
        });
    }
};
 

export const logout = () => async (dispatch) => {
    try {
        localStorage.removeItem('firstLogin')
        await postDataAPI('logout')
        window.location.href = "/login"
    } catch (err) {
        dispatch({ 
            type: GLOBALTYPES.ALERT, 
            payload: {
                error: err.response.data.msg
            } 
        })
    }
}